<template>
  <div>
       <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item><a href="/">用户管理</a></el-breadcrumb-item>
            <el-breadcrumb-item>活动列表</el-breadcrumb-item>
        </el-breadcrumb>
        <!-- 卡片试图 -->
        <el-card class="box-card">
           <!-- 搜索与添加 -->
            <el-row :gutter="20">
                <el-col :span="7">
                    <el-input clearable @clear="getUserList" placeholder="请输入内容" v-model="queryInfo.query">
                        <el-button @click="getUserList" slot="append" icon="el-icon-search"></el-button>
                    </el-input>
                </el-col>
                <el-col :span="4">
                    <el-button type="primary" @click="addDialogVisible=true">添加用户</el-button>
                </el-col>
            </el-row>
            <!-- 用户列表区 -->
            <el-table :data="userlist" border stripe>
                <el-table-column label="#" type="index"></el-table-column>
                <el-table-column label="姓名" prop="username"></el-table-column>
                <el-table-column label="邮箱" prop="email"></el-table-column>
                <el-table-column label="电话" prop="mobile"></el-table-column>
                <el-table-column label="角色" prop="role_name"></el-table-column>
                <el-table-column label="状态">
                    <template slot-scope="scope">
                        <!-- {{ scope.row }} -->
                        <!-- 开关状态 -->
                        <el-switch @change="userStatechange(scope.row)" v-model="scope.row.mg_state">
                        </el-switch>
                    </template>
                </el-table-column>
                <el-table-column label="操作" width="180px">
                    <template slot-scope="scope">
                        <!-- 修改按钮 -->
                        <el-button @click="showEditDialog(scope.row.id)" type="primary" icon="el-icon-edit" size="mini"></el-button>
                        <!-- 删除按钮 -->
                        <el-button @click="removeUser(scope.row.id)" type="danger" icon="el-icon-delete" size="mini"></el-button>
                        <el-tooltip :enterable="false" class="item" effect="dark" content="分配角色" placement="top">
                            <el-button type="warning" icon="el-icon-setting" size="mini"></el-button>
                        </el-tooltip>
                    </template>
                </el-table-column>
            </el-table>
            <!-- 分页区域 -->
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="queryInfo.pagenum"
                :page-sizes="[1, 2, 5, 10]"
                :page-size="queryInfo.pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>

        </el-card>
<!-- 点击添加用户出现的模态框 -->
        <el-dialog
        @close="addDialogClose"
            title="添加用户"
            :visible.sync="addDialogVisible"
            width="50%">    
           <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px">
                <el-form-item label="用户名" prop="username">
                    <el-input v-model="addForm.username"></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="password">
                    <el-input v-model="addForm.password"></el-input>
                </el-form-item>
                <el-form-item label="邮箱" prop="email">
                    <el-input v-model="addForm.email"></el-input>
                </el-form-item>
                <el-form-item label="手机号" prop="mobile">
                    <el-input v-model="addForm.mobile"></el-input>
                </el-form-item>
            </el-form>
            <!-- 底部按钮区 -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="addDialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="adduser">确 定</el-button>
            </span>
        </el-dialog>
<!-- 点击编辑出现的编辑模态框 -->
        <el-dialog
                title="修改用户"
                :visible.sync="editDialogVisible"
                width="50%" @close="editDialogClosed">
            <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px">
                <el-form-item label="用户名">
                    <el-input v-model="editForm.username" disabled></el-input>
                </el-form-item>
                <el-form-item label="邮箱" prop="email">
                    <el-input v-model="editForm.email"></el-input>
                </el-form-item>
                <el-form-item label="手机号" prop="mobile">
                    <el-input v-model="editForm.mobile"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="editDialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="editUserInfo">确 定</el-button>
            </span>
        </el-dialog>


  </div>    
</template>

<script>
export default {
  name: '',
  data() { 
    // 验证邮箱正则
    var checkEmail=(rule,value,cb)=>{
         const regEmail=/^([a-zA-z0-9_-])+@([a-zA-z0-9_-])+(\.[a-zA-z0-9_-])/
         if(regEmail.test(value)){
            return cb()
         }
         cb(new Error('请输入合法邮箱'))

    }
    var checkMobile=(rule,value,cb)=>{
        const regMobile=/^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
         if(regMobile.test(value)){
            return cb()
         }
         cb(new Error('请输入合法手机号'))
    }
    return {
       queryInfo:{
           query:'',
           pagenum:1,    //当前显示第几页
           pagesize:2,   //当前每页显示多少条数据
       },
       userlist:[],
       total:0,
       addDialogVisible:false, //控制添加用户对话框的显示隐藏
       addForm:{
           username:'',
           password:'',
           email:'',
           mobile:'',
       },   //添加用户的表单数据
       addFormRules:{
           username:[
               {
                   required:true,
                   message:'请输入用户名',
                   trigger:'blur'
               },
               {
                min:3,
                max:10,
                message:'用户名的长度在3-10个字符之间',
                trigger:'blur'
               },

           ],
           password:[
               {
                   required:true,
                   message:'请输入密码',
                   trigger:'blur'
               },
               {
                min:6,
                max:15,
                message:'密码的长度在6-15个字符之间',
                trigger:'blur'
               },
           ],
             email:[
               {
                   required:true,
                   message:'请输入邮箱',
                   trigger:'blur'
               },
               {
                   validator:checkEmail,
                   trigger:'blur'
               }
           ],
            mobile:[
               {
                   required:true,
                   message:'请输入手机',
                   trigger:'blur'
               },
               {
                   validator:checkMobile,
                   trigger:'blur'
               }
           ]
       },  //添加表单的验证规则对象
       editDialogVisible:false,   //控制修改用户对话框的显示隐藏
       editForm:{},   //查询到的用户信息对象
       editFormRules:{
           email:[
               {
                   required:true,
                   message:'请输入邮箱',
                   trigger:'blur'
               },
               {
                   validator:checkEmail,
                   trigger:'blur'
               }
           ],
           mobile:[
               {
                   required:true,
                   message:'请输入手机',
                   trigger:'blur'
               },
               {
                   validator:checkMobile,
                   trigger:'blur'
               }
           ]
       }
     
    }
  },
  props: {

  },
  components:{
  },
  created(){
     this.getUserList()
  },
  mounted() {

  },
  methods:{
    async getUserList(){
        const { data:res } = await this.$axios.get('users',{params:this.queryInfo})
        if(res.meta.status!==200) return this.$Message.error('获取信息失败')
        console.log(res)
        this.userlist=res.data.users
        this.total=res.data.total
    },
    // 监听pagesize改变的事件分页
    handleSizeChange(newsize){
        console.log(newsize)
        this.queryInfo.pagesize=newsize
        this.getUserList()
    },
    // 监听的页码值发生改变
    handleCurrentChange(newPage){
       this.queryInfo.pagenum=newPage
        this.getUserList()
    },
    // 监听switch开关状态的改变
    async userStatechange(userinfo){
       const { data:res } = await this.$axios.put(`users/${userinfo.id}/state/${userinfo.mg_state}`)
       if(res.meta.status!==200){
           userinfo.mg_state=!userinfo.mg_state
           return this.$message.error("更新用户状态失败")
       }
       this.$message.success("更新用户状态成功")

    },
    // 再次打开模态框是个干净的初始的状态，重置
    addDialogClose(){
       this.$refs.addFormRef.resetFields()
    },
    // 点击确定按钮添加新用户
     adduser(){
       this.$refs.addFormRef.validate(async valid=>{
        //    console.log(valid)
        if(!valid) return;
        // 成功就可以发起添加用户请求
        const { data:res }=await this.$axios.post('users',this.addForm)
        if(res.meta.status!==201){
            this.$message.error("添加用户失败")
        }
        this.$message.success("添加用户成功")
        this.addDialogVisible=false
        this.getUserList()
       })
    },
    // 点击编辑的方法
    async showEditDialog(id){
         console.log(id)
         const { data:res } = await this.$axios.get('users/'+id)
         if(res.meta.status!==200){
             return this.$message.error('查询用户信息失败')
         }
         this.editForm=res.data
         this.editDialogVisible=true

    },
    // 重置修改对话框关闭在打开的内容
    editDialogClosed(){
           this.$refs.editFormRef.resetFields()
    },
    // 修改数据
    editUserInfo(){
        this.$refs.editFormRef.validate( async valid=>{
            if(!valid) return 
            // 请求接口
            const { data:res }= await this.$axios.put('users/'+this.editForm.id,{
                email:this.editForm.email,
                mobile:this.editForm.mobile
            })
            if(res.meta.status!==200){
                return this.$message.error('更新用户信息失败')
            }
            // 关闭对话框
            this.editDialogVisible=false
            // 刷新用户列表
            this.getUserList()
            this.$message.success('更新数据成功')
            
        })
    },
    // 删除
    async removeUser(id){
        console.log(id)
       //    先弹框确认是否删除
       const confrimReault = await this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).catch(err=>{
            return err
        })
        if(confrimReault!=="confirm"){
            return this.$message.info('已经取消删除了')
        }
        const {data:res} = await this.$axios.delete('users/'+id)
        console.log(res)
        if(res.meta.status!==200){
           return this.$message.error('删除失败')
        }
        this.$message.success('删除成功')
        this.getUserList()
    }

  },
 }
</script>

<style scoped>
.el-breadcrumb{
    margin-bottom:15px;
    font-size:12px;
}
.el-card{
    box-shadow: 0px 1px 1px rgba(0,0,0,0.15) !important;
}
.el-table{
    margin-top:15px;
    font-size:12px;
}
.el-pagination{
    margin-top:15px;
}
</style>