<template>
  <div>
       <!-- 面包屑导航 -->
       <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item><a href="/">权限管理</a></el-breadcrumb-item>
            <el-breadcrumb-item>角色列表</el-breadcrumb-item>
        </el-breadcrumb>

        <!-- 卡片试图 -->
        <el-card>
            <!-- 添加角色 -->
            <el-row>
                <el-col>
                    <el-button type="primary" @click="addDialogVisible=true">添加角色</el-button>
                </el-col>
            </el-row>
            <!-- 角色列表 -->
            <el-table :data="rolelist" border stripe>
                <!-- 展开列 -->
                <el-table-column type="expand">
                    <template slot-scope="scope">
                        <el-row :class="['bdbottom',i1===0?'bdtop':'','vcenter']" v-for="(item1,i1) in scope.row.children" :key="item1.id">
                            <!-- 渲染一级权限 -->
                            <el-col :span="5">
                                <el-tag @close="removeRightById(scope.row,item1.id)" closable>{{ item1.authName }}</el-tag>
                                <i class="el-icon-caret-right"></i>
                            </el-col>
                            <!-- 渲染二级权限 -->
                            <el-col :span="19">
                                <!-- 嵌套渲染二级权限 -->
                                <el-row :class="[i2===0?'':'bdtop','vcenter']" v-for="(item2,i2) in item1.children" :key="item2.id">
                                    <!-- 二级 -->
                                    <el-col :span="6">
                                        <el-tag @close="removeRightById(scope.row,item2.id)" closable type="success">{{ item2.authName }}</el-tag>
                                        <i class="el-icon-caret-right"></i>
                                    </el-col>
                                    <!-- 三级 -->
                                    <el-col :span="18">
                                        <el-tag @close="removeRightById(scope.row,item3.id)" closable type="warning" v-for="(item3,i3) in item2.children" :key="item3.id">{{ item3.authName }}</el-tag>
                                    </el-col>
                                </el-row>
                            </el-col>
                        </el-row>
                    </template>
                </el-table-column>
                <!-- 索引列 -->
                 <el-table-column type="index" label="#"></el-table-column>
                 <el-table-column label="角色名称" prop="roleName"></el-table-column>
                 <el-table-column label="角色描述" prop="roleDesc"></el-table-column>
                 <el-table-column label="操作" width="300">
                     <template slot-scope="scope">
                            <el-button @click="xiunum(scope.row.id)" size="mini" type="primary" icon="el-icon-edit">编辑</el-button>
                            <el-button @click="removeUser(scope.row.id)" size="mini" type="danger" icon="el-icon-delete">删除</el-button>
                            <el-button size="mini" type="warning" icon="el-icon-setting" @click="showSetRightDialog">分配权限</el-button>
                     </template>
                 </el-table-column>
            </el-table>
        </el-card>
       <!-- 点击添加角色出现的对话框 -->
         <el-dialog
         @close="addrolesClose"
         :visible.sync="addDialogVisible"
            title="添加角色"
            width="50%">    
           <el-form :model="rolesForm" label-width="70px" ref="addFormRef">
                <el-form-item label="角色名称" prop="roleName">
                    <el-input v-model="rolesForm.roleName"></el-input>
                </el-form-item>
                <el-form-item label="角色描述" prop="roleDesc">
                    <el-input v-model="rolesForm.roleDesc"></el-input>
                </el-form-item>
            </el-form>
            <!-- 底部按钮区 -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="addDialogVisible=false">取 消</el-button>
                <el-button type="primary" @click="addroles">确 定</el-button>
            </span>
        </el-dialog>

        <!-- 点击编辑出现的对话框 -->
         <el-dialog
                title="修改角色"
                :visible.sync="editDialogVisible"
                width="50%" @close="editDialogClosed">
            <el-form :model="editForm"  ref="editFormRef" label-width="70px">
                <el-form-item label="角色名称" prop="roleName">
                    <el-input v-model="editForm.roleName"></el-input>
                </el-form-item>
                <el-form-item label="角色描述" prop="roleDesc">
                    <el-input v-model="editForm.roleDesc"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="editDialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="editUserInfo">确 定</el-button>
            </span>
        </el-dialog>

       <!-- 分配权限的对话框 -->
        <el-dialog
            title="分配权限"
            :visible.sync="setRightDialogVisible"
            width="50%">
            <!-- 树形控件 -->
            <el-tree :data="rightlist" :props="treeprops"></el-tree>
            <span slot="footer" class="dialog-footer">
                <el-button @click="setRightDialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="setRightDialogVisible = false">确 定</el-button>
            </span>
            </el-dialog>

  </div>
</template>

<script>
export default {
  name: '',
  data() { 
    return {
         rolelist:[],
         addDialogVisible:false,  //控制添加对话框的显示隐藏
         rolesForm:{},
         editDialogVisible:false,  //控制编辑对话框的显示隐藏
         editForm:{},
         setRightDialogVisible:false,   //分配权限的对话框
         rightlist:[],  //所有权限列表数据
         treeprops:{
             label:'authName',
             children:'children'
         }
    }
  },
  props: {

  },
  created(){
     this.getRolesList()
  },
  components:{
  },
  mounted() {

  },
  methods:{
    async getRolesList(){
        const { data:res } = await this.$axios.get('roles')
        if(res.meta.status!==200){
            return this.$message.error('获取列表失败')
        }
        this.rolelist=res.data
        console.log(this.rolelist)
    },
    addrolesClose(){
        this.$refs.addFormRef.resetFields()
    },
    // 点击确认添加角色
    addroles(){
        this.$refs.addFormRef.validate(async valid=>{
        //    console.log(valid)
        if(!valid) return;
        // 成功就可以发起添加用户请求
        const { data:res }=await this.$axios.post('roles',this.rolesForm)
        if(res.meta.status!==201){
            this.$message.error("添加用户失败")
        }
        this.$message.success("添加用户成功")
        this.addDialogVisible=false
        this.getRolesList()
       })
    },
    // xiunum(){
    //     this.editDialogVisible=true
    // },
    editDialogClosed(){
        this.$refs.editFormRef.resetFields()
    },
    // 点击编辑
    async xiunum(id){
         console.log(id)
         const { data:res } = await this.$axios.get('roles/'+id)
         if(res.meta.status!==200){
             return this.$message.error('查询用户信息失败')
         }
         this.editForm=res.data
         console.log( this.editForm)
         this.editDialogVisible=true

    },
    // 点击编辑里的确认
    editUserInfo(){
        this.$refs.editFormRef.validate( async valid=>{
            if(!valid) return 
            // 请求接口
            const { data:res }= await this.$axios.put('roles/'+this.editForm.roleId,{
                roleName:this.editForm.roleName,
                roleDesc:this.editForm.roleDesc
            })
            console.log(res)
            if(res.meta.status!==200){
                return this.$message.error('更新用户信息失败')
            }
            // 关闭对话框
            this.editDialogVisible=false
            // 刷新用户列表
            this.getRolesList()
            this.$message.success('更新数据成功')
            
        })
    },
    // 点击删除
    async removeUser(id){
        console.log(id)
       //    先弹框确认是否删除
       const confrimReault = await this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).catch(err=>{
            return err
        })
        if(confrimReault!=="confirm"){
            return this.$message.info('已经取消删除了')
        }
        const {data:res} = await this.$axios.delete('roles/'+id)
        console.log(res)
        if(res.meta.status!==200){
           return this.$message.error('删除失败')
        }
        this.$message.success('删除成功')
        this.getRolesList()
    },
    async removeRightById(role,rightId){
        // 提示用户是否删除
       const confirmResult = await this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).catch(err=>err)
        if(confirmResult!=='confirm'){
            return this.$message.info('取消了删除')
        }
        const {data:res} = await this.$axios.delete(`roles/${role.id}/rights/${rightId}`)
        if(res.meta.status!==200){
            return this.$message.error("删除权限失败")
        }
        // this.getRolesList()
        role.children=res.data
    },
    // 点击分配权限出现的对话框   TODO
    async showSetRightDialog(){
        // 获取权限数据
        const { data:res } = await this.$axios.get('rights/tree')
         if(res.meta.status!==200){
                return this.$message.error('获取数据失败')
            }
        this.rightlist=res.data
        console.log(this.rightlist)
        this.setRightDialogVisible=true
    }

  },
 }
</script>

<style scoped>
.el-card{
    margin-top:15px;
}
.el-tag{
    margin:7px;
}
.bdtop{
    border-top:1px solid #eee;
}
.bdbottom{
    border-bottom:1px solid #eee;
}
.vcenter{
    display: flex;
    align-items: center;
}
</style>