<template>
  <div class="great">
    <el-container class="great-container">
        <!-- 头部 -->
        <el-header>
            <div>
                <img src="../assets/heima.png" alt="">
                <span>电商后台管理系统</span>
            </div>
            <el-button type="info" @click="loginout">退出</el-button></el-header>
        <!-- 页面主体区 -->
        <el-container>
            <!-- 侧边栏 -->
            <el-aside :width="isCollapse?'64px':'200px'">
                <div class="toggle_button" @click="toogleCollapse">|||</div>
                <!-- :unique-opened="true"实现每次只展开一项 -->
                <!-- router开启路由模式 -->
                <el-menu :default-active="activePath" router :collapse-transition="false" :collapse="isCollapse" :unique-opened="true" background-color="#333744" text-color="#fff" active-text-color="#409eff">
                    <!-- 一级菜单 -->
                    <el-submenu :index="item.id+''" v-for="item in menuList" :key="item.id">
                        <!-- 一级菜单的模板区 -->
                        <template slot="title">
                            <!-- 图标 -->
                            <i :class="iconsobj[item.id]"></i>
                            <span>{{ item.authName }}</span>
                        </template>
                        <!-- 二级菜单 -->
                        <el-menu-item @click="saveNavState('/'+subitem.path)" :index="'/'+subitem.path" v-for="subitem in item.children" :key="subitem.id">
                            <!-- 二级菜单的图标文本 -->
                            <template slot="title">
                                <!-- 图标 -->
                                <i class="el-icon-menu"></i>
                                <span>{{ subitem.authName }}</span>
                            </template>
                        </el-menu-item>
                    </el-submenu>
                </el-menu>
            </el-aside>

            <!-- 内容区 -->
            <el-main>
                <router-view></router-view>
            </el-main>
        </el-container>
    </el-container>
  </div>
</template>

<script>
export default {
  name: '',
  data() { 
    return {
        menuList:[],   //左侧菜单数据
        iconsobj:{
            '125':'iconfont icon-user',
            '103':'iconfont icon-tijikongjian',
            '101':'iconfont icon-shangpin',
            '102':'iconfont icon-danju',
            '145':'iconfont icon-baobiao',
        },
        isCollapse:false,   //是否折叠左侧菜单
        activePath:'',   //被激活的链接地址
    }
  },
  props: {

  },
  components:{
  },
  mounted() {

  },
  created(){
     this.getMenuList(),  //调用左侧菜单列表接口方法
     this.activePath=window.sessionStorage.getItem("activePath")
  },
  methods:{
    // 点击退出登录
    loginout(){
        window.sessionStorage.clear()
        this.$router.push('/')
    },
    // 请求左侧菜单列表接口方法
    async getMenuList(){
        let { data:res} = await this.$axios.get('menus')
        console.log(res)
        if(res.meta.status!==200) return this.$message.error(res.meta.status)
        this.menuList=res.data
        console.log(this.menuList)
    },
    // 点击实现左侧菜单的折叠和展开
    toogleCollapse(){
        this.isCollapse=!this.isCollapse
    },
    // 保存链接的激活状态
    saveNavState(activePath){
        window.sessionStorage.setItem("activePath",activePath)
        this.activePath=activePath
    }

  },
 }
</script>

<style scoped>
.great{
   height:100%;
}
.great-container{
    height:100%;
}
.el-header{
    background-color: #373d41;
    display: flex;
    justify-content: space-between;
    padding-left:0;
    align-items: center;
    color:#fff;
    font-size:20px;
}
.el-header>div{
    display: flex;
    align-items: center;
}
.el-header>div>span{
    margin-left:15px;
}
.el-aside{
    background-color: #333744;
}
.el-main{
    background-color: #eaedf1;
}
.iconfont{
    margin-right:10px;
}
.el-menu{
    border-right:none;
}
.toggle_button{
    background-color: #4a5064;
    color:#fff;
    font-size:10px;
    line-height:24px;
    text-align: center;
    /* 让三条线之间离远些 */
    letter-spacing: 0.2em;
    cursor: pointer;
}
</style>